<style scoped lang="less">
  .chart {
    height: 100%;
    h3 {
      font-size: 50px;
      margin-top: 20%;
    }
    .chart-nav {
      display: flex;
      li {
        flex: 1;
        height: 30px;
        line-height: 30px;
        &:hover,
        &.act {
          background: #20a0ff;
          color: #fff;
          cursor: default;
        }
      }
    }
    .echartslist {
      position: absolute;
      top: 80px;
      left: 1.5%;
      right: 1.5%;
      bottom: 60px;
      margin: auto;
      overflow: auto;
      text-align: center;
      background: #ccc;
    }
  }
</style>
<template>
  <div class="statistics content-layout">
    <div class="content-header">
      <div class="content-btn"></div>
      <header-search :seachData="seachData" @seach="seachFn"></header-search>
    </div>
    <div class="content-main">
      <div class="chart">
        <ol class="chart-nav">
          <li @click="showRecordNumber(index)" :class="{act: act == index? 'act' : ''}" v-for="(navItem,index) in chartNavData">{{navItem.text}}</li>
        </ol>
        <div class="echartslist" id="echarsdom"></div>
      </div>
    </div>
  </div>
</template>
<script>
  import echarts from 'echarts/dist/echarts.min'
  import headerSearch from '../../../public/header-search'
  import searchDataModule from '../../../../api/searchDataModule'
  import searchInitDefault from '../../../../api/searchInitDefault'
  export default {
    name: '',
    data(){
      return {
        act: '',
        seachData: {
          starttime: searchDataModule.starttime,
          endtime: searchDataModule.endtime,
          zjval: searchDataModule.zjval,
          zj: searchDataModule.zj,
          qyval:searchDataModule.qyval,
          qy:searchDataModule.qy,
          lxval: searchDataModule.lxval,
          lx: searchDataModule.lx,
          seachInfo: searchDataModule.seachInfo
        },
        chartNavData: [
          {"text": '明细统计'},
          {"text": '报表'}
        ],
        //报告及时率
        timelyRate:{
          title: {
            text: '多雷达图'
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            x: 'center',
            data:['某软件','某主食手机','某水果手机','降水量','蒸发量']
          },
          radar: [
            {
              indicator: [
                {text: '品牌', max: 100},
                {text: '内容', max: 100},
                {text: '可用性', max: 100},
                {text: '功能', max: 100}
              ],
              center: ['25%','40%'],
              radius: 80
            },
            {
              indicator: [
                {text: '外观', max: 100},
                {text: '拍照', max: 100},
                {text: '系统', max: 100},
                {text: '性能', max: 100},
                {text: '屏幕', max: 100}
              ],
              radius: 80,
              center: ['50%','60%'],
            },
            {
              indicator: (function (){
                var res = [];
                for (var i = 1; i <= 12; i++) {
                  res.push({text:i+'月',max:100});
                }
                return res;
              })(),
              center: ['75%','40%'],
              radius: 80
            }
          ],
          series: [
            {
              type: 'radar',
              tooltip: {
                trigger: 'item'
              },
              itemStyle: {normal: {areaStyle: {type: 'default'}}},
              data: [
                {
                  value: [60,73,85,40],
                  name: '某软件'
                }
              ]
            },
            {
              type: 'radar',
              radarIndex: 1,
              data: [
                {
                  value: [85, 90, 90, 95, 95],
                  name: '某主食手机'
                },
                {
                  value: [95, 80, 95, 90, 93],
                  name: '某水果手机'
                }
              ]
            },
            {
              type: 'radar',
              radarIndex: 2,
              itemStyle: {normal: {areaStyle: {type: 'default'}}},
              data: [
                {
                  name: '降水量',
                  value: [2.6, 5.9, 9.0, 26.4, 28.7, 70.7, 75.6, 82.2, 48.7, 18.8, 6.0, 2.3],
                },
                {
                  name:'蒸发量',
                  value:[2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 35.6, 62.2, 32.6, 20.0, 6.4, 3.3]
                }
              ]
            }
          ]
        },
        //延迟率
        delayRate: {
          title : {
            text: '某地区蒸发量和降水量',
            subtext: '纯属虚构'
          },
          tooltip : {
            trigger: 'axis'
          },
          legend: {
            data:['蒸发量','降水量']
          },
          toolbox: {
            show : true,
            feature : {
              dataView : {show: true, readOnly: false},
              magicType : {show: true, type: ['line', 'bar']},
              restore : {show: true},
              saveAsImage : {show: true}
            }
          },
          calculable : true,
          xAxis : [
            {
              type : 'category',
              data : ['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月']
            }
          ],
          yAxis : [
            {
              type : 'value'
            }
          ],
          series : [
            {
              name:'蒸发量',
              type:'bar',
              data:[2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6, 20.0, 6.4, 3.3],
              markPoint : {
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name: '平均值'}
                ]
              }
            },
            {
              name:'降水量',
              type:'bar',
              data:[2.6, 5.9, 9.0, 26.4, 28.7, 70.7, 175.6, 182.2, 48.7, 18.8, 6.0, 2.3],
              markPoint : {
                data : [
                  {name : '年最高', value : 182.2, xAxis: 7, yAxis: 183},
                  {name : '年最低', value : 2.3, xAxis: 11, yAxis: 3}
                ]
              },
              markLine : {
                data : [
                  {type : 'average', name : '平均值'}
                ]
              }
            }
          ]
        },
        myecharts: null
      }
    },
    props: ['a'],
    components: {
      headerSearch
    },
    mounted(){
      searchInitDefault(searchDataModule);
      this.myecharts = echarts.init(document.getElementById('echarsdom'));
      this.showRecordNumber(0);
    },
    methods: {
      seachFn(val){
        console.log(this.seachData)
      },
      showRecordNumber(i){
        var opt = {};
        this.act = i
        if(i == 0){
          opt = this.delayRate;
        }else{
          opt = this.timelyRate;
        }
        this.myecharts.setOption(opt,true);
      }
    }
  }
</script>



